.header-container{
    xborder:1px dashed red;
    display:flex;
    align-items: stretch;
    flex-direction: row;
    margin-left: 16px;
    margin-right: 16px;

}
.header-left{
    flex-grow: 1;
    line-height: 36px;
    font-size:24px;
    padding: 4px;
}
.header-middle{
    text-align: center;
    flex-grow: 1;
    line-height: 36px;
    font-size:28px;
    padding: 4px;
}
.header-right{
    text-align: right;
    flex-grow: 1;
    line-height: 36px;
    xborder:1px dashed green;
align-self: center;

}
